import React, { Component, useCallback, useEffect, useState, useRef } from "react";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import styled from 'styled-components'
import Login from './Login'
import Shoebill from "./Shoebill";

class Admin extends Component {
    state = {
        isLogin: false
    }

    loginHandler = () => {
        this.setState({isLogin: !this.state.isLogin})
    }


    render () {
        return (
            <div>
                {this.state.isLogin || <Login isLogin={this.state.isLogin} loginHandler={this.loginHandler} />}
                {this.state.isLogin && <Shoebill />}


            </div>

        )
    }
}

export default Admin

